import React from 'react'
import styles from './style'
import { Grid, Typography,List,ListItem,Collapse } from '@material-ui/core'

export default function News(props){
const classes=styles()
const value=props.value
    return(
        <Grid container className={classes.root} xs={12}>
                <Grid item className={classes.paddings}>
                    <img className={classes.img} src="data:image/png;base64," />
                </Grid>
    <Typography className={classes.title}>{value.title}</Typography>
    {value.data.map((val)=>
            <Grid container className={classes.container}>
            <Grid item className={classes.firstGrid} xs={3}>
               <img className={classes.innerimg} src={val.img} />
            </Grid> 
            <Grid item className={classes.secGrid} xs={9}>
            {val.para}
            </Grid>
        </Grid>
    )}
    
        </Grid>

       
    )
}